import { NavLink } from "react-router-dom";

export const Navigation = () => {
  return (
    <nav>
      <ul>
        <li>
          <NavLink to="/"> </NavLink>
        </li>
        <li>
          <NavLink to="/products"></NavLink>
        </li>
        <li>
          <NavLink to="/materials"></NavLink>
        </li>
      </ul>
    </nav>
  );
};
